/**forms**/
@keyframes form-cb__form-show {
  0% {
    display: none;
    background: none;
    background: transparent;
  }
  50% {
    display: flex;
    z-index: 100;
  }
  100% {
    opacity: 1;
    z-index: 100;
  }
}

@keyframes form-cb__form-hide {
  0% {
    display: flex;
    z-index: 99;
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
  99% {
    opacity: 0;
  }
  100% {
    opacity: 0;
    display: none;
  }
}

@keyframes form-cb__form-wrap-show {
  0% {
    display: none;
    transform: scale(0.1);
  }
  20% {
    display: flex;
    z-index: 100;
    opacity: 1;
  }
  100% {
    z-index: 100;
  }
}

@keyframes form-cb__form-wrap-hide {
  0% {
    display: none;
  }
  20% {
    display: flex;
    z-index: 100;
    opacity: 1;
  }
  100% {
    transform: scale(0.1);
    z-index: 100;
  }
}

